import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DirectorComponent } from './accounts/director/director.component';
import { ManagerComponent } from './accounts/manager/manager.component';
import { OwnerComponent } from './accounts/owner/owner.component';
import { HomeLandingPageComponent } from './home-page/home-landing-page/home-landing-page.component';
import { HomePageComponent } from './home-page/home-page.component';

const routes: Routes = [
  {
    path: '', component: HomePageComponent, children: [
      { path: '', redirectTo: '/', pathMatch: 'full' },
      { path: '', component: HomeLandingPageComponent },
      { path: 'owner', component: OwnerComponent },
      { path: 'director', component: DirectorComponent },
      { path: 'manager', component: ManagerComponent }
    ]
  }

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {

}
